import React from "react";
import './SidebarOption.css';

function SidebarOption( {active, Icon, text} ) 
{                                               /* -->Irops is a component. In react icon, text, CSS styling etc. can be passed as an argument in an function. 
                                                Icon ka 'I' capital h....because it is a component. */    
  return (                                      /* also jab tune function mai argument pass kiye then keep in mid the syntax function func_name ({arg1, arg2}) { func_content } */
    <div className={`sidebarOption ${active
        && 'sidebarOption--active'}`}>
        <Icon/>
        <h2 id="text_for_icons">{text}</h2>
    </div>
  )
}

export default SidebarOption;